const KEY = '19123568-82b273ca09da88aba59706b54';
const BASE_URL = `https://pixabay.com/api/`;

export default class ApiService {
  constructor() {
    this.page = 1;
    this.searchQuery = '';
    }
    
  async fetchImages() {
    const url = `${BASE_URL}?image_type=photo&orientation=horizontal&q=${this.searchQuery}&page=${this.page}&per_page=12&key=${KEY}`;

    return this.getData(url).then(response => {
      return response.hits;
    });
  }

  async countImages() {
    const url = `${BASE_URL}?image_type=photo&orientation=horizontal&q=${this.searchQuery}&page=${this.page}&per_page=12&key=${KEY}`;

    return this.getData(url).then(response => {
      return response.totalHits;
    });
  }

  async getData(url) {
    const images = await fetch(url);
    const response = await images.json();
    return response;
  }

  get searchQuery() {
    return this.searchQuery;
  }

  set searchQuery(newSearchQuery) {
    this.searchQuery = newSearchQuery;
  }

  get page() {
    return this.page;
  }

  set page(newPage) {
    this.page = newPage;
  }
}